// axios
import axios from 'axios'
import NProgress from 'nprogress'
import Event from './event'

export const emitter = new Event()
let cancel = {}
const CancelToken = axios.CancelToken

axios.defaults.baseURL = '/'
axios.defaults.headers = {
  'Content-Type': 'application/json; charset=utf-8'
}
axios.defaults.timeout = 5000
// 
// axios.interceptors.request.use(config => {
//   // 
//   if (promiseArr[config.url]) {
//     promiseArr[config.url]('')
//     promiseArr[config.url] = cancel
//   } else {
//     promiseArr[config.url] = cancel
//   }
//   return config
// }, error => {
//   return Promise.reject(error)
// })

// 
axios.interceptors.response.use(response => {
  // 
  if (response.data.code === '500') {
    emitter.emit('paramError')
    response.data.code = '100000'
  } else if (response.data.code === '900') {
    emitter.emit('requestError')
    response.data.code = '100000'
  }
  return response
}, err => {
  if (err && err.response) {
    switch (err.response.status) {
      case 403:
        // err.message = ''
        emitter.emit('noPermission')
        break
      case 404:
        // 511404
        // err.message = ''
        emitter.emit('noLogin')
        break
      case 511:
        // err.message = ''
        emitter.emit('noLogin')
        break
      default:
        emitter.emit('errorOther')
        // err.message = `${err.response.status}`
    }
  } else {
    emitter.emit('errorServer')
    // err.message = ''
  }
  // message.err(err.message)
  return Promise.resolve(err.response)
})

export default {
  // get
  get (url, param, callback) {
    NProgress.start()
    return new Promise((resolve, reject) => {
      axios({
        method: 'get',
        url,
        params: param,
        cancelToken: new CancelToken(c => {
          cancel = c
        })
      }).then(res => {
        // resolve(res)
        NProgress.done()
        if (res && callback && typeof callback === 'function') {
          callback(res)
        }
      })
    })
  },
  // post
  post (url, param, callback) {
    NProgress.start()
    return new Promise((resolve, reject) => {
      axios({
        method: 'post',
        url,
        data: param,
        cancelToken: new CancelToken(c => {
          cancel = c
        })
      }).then(res => {
        // resolve(res)
        NProgress.done()
        if (res && callback && typeof callback === 'function') {
          callback(res)
        }
      })
    })
  }
}



